import "../styles/suggestions.scss"
import Profile from "./Profile";
import s1  from "../images/suggestion/1.JPG"
import s2  from "../images/suggestion/2.JPG"

function Suggestions()
{
    return(
        <div className="suggestions">
                  <div className="titleContainer">
        <div className="title">Suggestions For You</div>
        <a href="/">See All</a>
      </div>

      <Profile
        username="pattikuttan"
        caption="Followed by basilleju + 3 more"
        urlText="Follow"
        iconSize="medium"
        captionSize="small"
        storyBorder={true}
        image={s1}
      />
      <Profile
        username="kozhikunju"
        caption="Followed by heir_of_dreams_ayisha_ + 1 more"
        urlText="Follow"
        iconSize="medium"
        captionSize="small"
        image={s2}
      />



    </div>


    )
}

export default Suggestions;






